<script>
import dbService from "./fbase.js";

export default {
  name: 'App',
  data() {
    return {
      Qna_list: [],
      Question_list: [],
      create_Question: "",
      create_Answer: "",
      update_Question: "",
      update_Answer: "",
      update_Id: "",
      delete_Id: "",
      is_update_modal: true,
      is_Question: false,
    }
  },
  methods : {
    create_Qna(e) {
      e.preventDefault();
      for (let i=0; i< this.Question_list.length; i++) {
        if (this.Question_list[i] === this.create_Question) {
          this.is_Question = true;
          break
        } else {
          this.is_Question= false;
        }
      }
      if (this.create_Question !== "" && this.create_Answer !== "") {
        if (this.is_Question) {
          alert(`${this.create_Question}의 대한 답은 이미 있습니다!`)
          this.is_Question = false;
        } else {
          dbService.collection("Qna")
              .add({
                Question: this.create_Question,
                Answer: this.create_Answer
              })
          alert("Qna 추가 완료!");
          this.is_Question = false;
        }
      } else {
        alert(`공백은 질문또는 답이 될 수 없습니다!`)
        this.is_Question = false;
      }
    },
    read_Qna() {
      dbService.collection("Qna").onSnapshot((snapshot) => {
        this.Qna_list = [];
        this.Question_list = [];
        snapshot.docs.forEach((doc) => {
          this.Qna_list.push({
            id: doc.id,
            Question: doc.data().Question,
            Answer: doc.data().Answer,
          });
          this.Question_list.push(doc.data().Question)
        });
        return this.Qna_list
      })
    },
    update_Qna(e) {
      e.preventDefault();
      dbService.doc(`Qna/${this.update_Id}`).update({
        Answer: this.update_Answer
      });
      alert("업데이트 성공!")
    },
    delete_Qna(e) {
      e.preventDefault();
      dbService.doc(`Qna/${this.delete_Id}`).delete();
      alert("삭제 성공!")
    },
    on_update_Answer() {
      if (this.update_Answer === "") {
        this.is_update_modal = false
      } else {
        this.is_update_modal =true
      }
    }
  },

  mounted() {
    this.read_Qna();
  },
}
</script>


<template>
  <div>
    <!--    수정 모달창-->
    <div class="modal fade" id="update" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header" style="background-color: #3657ff">
            <h5 class="modal-title" id="exampleModalLabel">질문 수정</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            <div class="input-group mb-3">
              <input @change="on_update_Answer" v-model="update_Answer" type="text" class="form-control" placeholder="답변" aria-label="답변" aria-describedby="basic-addon1">
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
            <button v-show="is_update_modal" style="background-color: #3657ff" @click="update_Qna" type="button" class="btn btn-primary" data-bs-dismiss="modal">Save changes</button>
          </div>
        </div>
      </div>
    </div>
    <!--    삭제 확인 모달창-->
    <div class="modal fade" id="delete" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header" style="background-color: #ff3636">
            <h5 class="modal-title" id="exampleModalLabel">삭제확인</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            정말로 삭제 하시겠습니까?
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">아니오</button>
            <button style="background-color: #ff3636" data-bs-dismiss="modal" @click="delete_Qna" type="button" class="btn btn-primary">예</button>
          </div>
        </div>
      </div>
    </div>
    <!--    질문 리스트-->
    <div style="margin-top: 5%">
      <div class="input-group mb-3" style="width: 80%; margin-right: 10%; margin-left: 10%; height: 5vh">
        <input v-model="create_Question" style="margin-right: 1%; width: 40%" type="text" class="form-control" placeholder="질문" aria-label="질문" aria-describedby="button-addon2">
        <input v-model="create_Answer" style="margin-right: 1%; width: 40%" type="text" class="form-control" placeholder="답변" aria-label="답변" aria-describedby="button-addon2">
        <button @click="create_Qna" style="border-radius: 10%" type="button" class="btn btn-outline-primary">
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-up-circle-fill" viewBox="0 0 16 16">
            <path d="M16 8A8 8 0 1 0 0 8a8 8 0 0 0 16 0zm-7.5 3.5a.5.5 0 0 1-1 0V5.707L5.354 7.854a.5.5 0 1 1-.708-.708l3-3a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1-.708.708L8.5 5.707V11.5z"/>
          </svg>
        </button>
      </div>
      <div v-for="a in Qna_list" :key="a.id" class="card border-dark mb-3" style="margin-top: 5%;width: 70%; margin-left: 15%; margin-right: 15%; overflow: hidden;text-overflow: ellipsis; white-space: nowrap;">
        <div class="card-header">{{ a.Question }}</div>
        <div class="card-body text-dark">
          <h5 class="card-title">{{ a.Answer }}</h5>
        </div>
        <div class="card-footer bg-transparent border-dark">
          <nav style="float: right;">
            <button @click="update_Id = a.id;update_Question=a.Question;update_Answer=a.Answer" data-bs-toggle="modal" data-bs-target="#update" type="button" class="btn btn-primary" style="background: none; border: none; ">
              <svg style="color:black;" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil" viewBox="0 0 16 16">
                <path d="M12.146.146a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1 0 .708l-10 10a.5.5 0 0 1-.168.11l-5 2a.5.5 0 0 1-.65-.65l2-5a.5.5 0 0 1 .11-.168l10-10zM11.207 2.5 13.5 4.793 14.793 3.5 12.5 1.207 11.207 2.5zm1.586 3L10.5 3.207 4 9.707V10h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.293l6.5-6.5zm-9.761 5.175-.106.106-1.528 3.821 3.821-1.528.106-.106A.5.5 0 0 1 5 12.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.468-.325z"/>
              </svg>
            </button>
            <button @click="delete_Id=a.id;" data-bs-toggle="modal" data-bs-target="#delete" type="button" class="btn btn-danger" style="background: none; border: none">
              <svg style="color: black" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-x-circle" viewBox="0 0 16 16">
                <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"/>
              </svg>
            </button>
          </nav>
        </div>
      </div>
    </div>
  </div>
</template>
